<template>
  <div id="taskEdit">
    <Modal
      v-model="taskEdit"
      title="">
      <Form ref="taskEditForm" :model="taskEditData" :rules="taskEditRule" :label-width="85" style="padding:0 20%;">
        <FormItem prop="cl001" label="ID" style="display: none">
          <Input v-model.trim="taskEditData.cl001"/>
        </FormItem>
        <FormItem prop="cl002" label="">
          <Input v-model.trim="taskEditData.cl002 " placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="cl016" label="">
          <Cascader :data="codeData" v-model="taskEditData.cl016" size="default" change-on-select></Cascader>
        </FormItem>
<!--        <FormItem prop="cl004" label="">-->
<!--          <i-select v-model.trim="taskEditData.cl004" style="width: 100%" placeholder="">-->
<!--            <i-option value="0"></i-option>-->
<!--            <i-option value="1"></i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="cl010" label="">
          <DatePicker size="default" type="year" :editable="false" v-model.trim="taskEditData.cl010" placement="bottom-end" placeholder="" style="width: 100%"></DatePicker>
        </FormItem>
<!--        <FormItem prop="cl011" label="">-->
<!--          <i-select v-model.trim="taskEditData.cl011" style="width: 100%" placeholder="">-->
<!--            <i-option value="1"></i-option>-->
<!--            <i-option value="2"></i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="ledgerIdList" label="">
          <i-select v-model.trim="taskEditData.ledgerIdList" multiple filterable style="width: 100%" placeholder="">
            <i-option v-for="item in editLedgerList" :value="item.value">{{ item.label }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl009" label="">
          <i-input v-model.trim="taskEditData.cl009" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=""></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="taskEditSubmit"></Button>
        <Button @click="closeModal"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
// 
import { renderYearTime } from '@/utils/common'
import {
  // 
  getLedgerList,
  // 
  updateTask
} from '@/api/taskManager'
export default {
  name: 'Taskedit',
  data () {
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      /*  */
      taskEditRule: {
        cl002: [
          { required: true, message: '', trigger: 'blur' }
        ],
        ledgerIdList: [
          { required: true, message: '', trigger: 'change', type: 'array' },
          { validator: validateCode, trigger: 'change' }
        ]
        // cl004: [
        //   { required: true, message: '', trigger: 'change', type: 'string' }
        // ]
      },
      /*  */
      taskEdit: false,
      // 
      taskEditData: {},
      // 
      editLedgerList: []
    }
  },
  props: {
    /*  */
    data: {},
    /*  */
    codeData: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  created () {
    this.getLedgerList()
  },
  methods: {
    // 
    async getLedgerList () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.editLedgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    dataHandle () {
      this.taskEditData.cl001 = this.data.cl001
      this.taskEditData.cl002 = this.data.cl002
      this.taskEditData.cl010 = this.data.cl010
      this.taskEditData.cl009 = this.data.cl009
      let row = this.data.rlhdGroupList
      let arr1 = []
      if (row) {
        if (row.length > 0) {
          row.forEach((item, index) => {
            arr1.push(item.id)
          })
        }
      }
      console.log(arr1)
      // $setselect
      this.$set(this.taskEditData, 'ledgerIdList', arr1)
      // this.$set(this.taskEditData, 'cl004', String(this.data.cl004))
      // this.$set(this.taskEditData, 'cl011', String(this.data.cl011))
      let arr = []
      this.getDefaultCode(this.codeData, String(this.data.cl016), arr)
    },
    /*  */
    getDefaultCode (data, code, arr) {
      for (let i in data) {
        if (data[i].value === code) {
          arr.push(data[i].value)
          this.taskEditData.cl016 = JSON.parse(JSON.stringify(arr))
        } else {
          if (data[0].children.length > 0) {
            arr.push(data[i].value)
            this.getDefaultCode(data[i].children, code, arr)
          }
        }
      }
      arr.pop()
    },
    /*  */
    taskEditSubmit () {
      this.$refs.taskEditForm.validate(valid => {
        if (valid) {
          let code = ''
          if (this.taskEditData.cl016 === undefined || this.taskEditData.cl016.length === 0) {
            code = ''
          } else {
            code = this.taskEditData.cl016[this.taskEditData.cl016.length - 1]
          }
          updateTask({
            data: {
              cl001: this.taskEditData.cl001,
              cl002: this.taskEditData.cl002,
              cl010: renderYearTime(this.taskEditData.cl010),
              ledgerIdList: this.taskEditData.ledgerIdList,
              cl009: this.taskEditData.cl009,
              cl016: code
              // cl004: this.taskEditData.cl004,
              // cl011: this.taskEditData.cl011
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              this.$emit('getTableData')
              this.taskEdit = false
              this.taskEditData = {}
              this.reset()
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch(() => {
            this.$Notice.error({ title: '' })
          })
        }
      })
    },
    // 
    reset () {
      this.taskEditData = {
        cl003: '',
        cl002: '',
        cl010: '',
        ledgerIdList: '',
        cl009: '',
        cl016: []
        // cl004: '',
        // cl011: ''
      }
      this.$refs.taskEditForm.resetFields()
    },
    closeModal () {
      this.taskEdit = false
     // this.reset()
    }
  }
}
</script>

