<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox @searchSrldId="searchSrldId" style="width: calc(100% - 370px);float: left;"></QueryBox>
<!--      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 340px)" ref="getQuery"></QueryBox>-->
      <div class="queryBtns">
<!--        <Button type="primary" size="small" icon="ios-paper-plane" class="btn-exportBtn" style="margin-right: 10px" @click.stop="handleIssue"></Button>-->
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.taskAdd.taskBuild = true"></Button>
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn"></Button>
        <Button type="success" size="small" icon="md-cloud-download" style="margin-right: 10px">
          <a href="epr\epr\image\.xls" style="color: #fff"></a>
        </Button>
        <Button type="success" size="small" class="btn-import" style="height: 24px;">
          <Upload
            class="upload-demo"
            action="/epr/api/task/importTask"
            name="file"
            :multiple="false"
            :on-success="uploadSuccess"
            :show-upload-list="false"
            accept=".xls"
            ref="upload">
            <Icon type="md-cloud-upload"></Icon>
            <span style="margin-left: 4px"></span>
          </Upload>
        </Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"
               ref="multipleTable"
               @on-selection-change="handleSelectionChange">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- -->
    <taskAdd ref="taskAdd" :codeData="codeData" @getTableData="getClearQueryData"></taskAdd>
    <!-- -->
    <taskEdit ref="taskEdit" :codeData="codeData" :data="editDefaultData" @getTableData="getTableList"></taskEdit>
    <!-- -->
    <IssueList ref="issueList" :taskIdArr="selectRow"></IssueList>
    <!-- -->
    <div class="spotsTable" :class="{addActive:addActive}">
      <spotsTable ref="spotsTable" :seeId="spotsSeeId" @signOut="signOut"></spotsTable>
    </div>
  </div>
</template>

<script>
import QueryBox from './areaQueryBox'
import taskAdd from './TaskAdd'
import taskEdit from './TaskEdit'
import IssueList from './IssueList'
import spotsTable from './spotsTable'
import { getMenu } from '@/api/acquisitionTable'
import {
  // 
  getTaskList,
  // 
  deleteTask,
  // 
  exportFile,
  // 
  importFile
} from '@/api/taskManager'
export default {
  name: 'extracTable',
  data () {
    return {
      /*  */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /*  */
      loading: false,
      /*  */
      columns1: [
        // {
        //   type: 'selection',
        //   width: 60,
        //   fixed: 'left',
        //   align: 'center'
        // },
        {
          title: '',
          key: 'index',
          width: 100,
          align: 'center',
          // fixed: 'left',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '',
          align: 'center',
          // fixed: 'left',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
        },
        // {
        //   title: '',
        //   align: 'center',
        //   key: 'cl004',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl004
        //     if (data === 0) {
        //       return h('span', {}, '')
        //     } else if (data === 1) {
        //       return h('span', {}, '')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '',
          align: 'center',
          width: 100,
          key: 'comName',
          render: (h, params) => {
            if (params.row.sysCompany && params.row.sysCompany.comName) {
              return h('span', {}, params.row.sysCompany.comName)
            } else {
              return h('span', {}, '')
            }
          }
        },
        {
          title: '',
          align: 'center',
          width: 100,
          key: 'cl010'
        },
        // {
        //   title: '',
        //   align: 'center',
        //   key: 'cl011',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl011
        //     if (data === 1) {
        //       return h('span', {}, '')
        //     } else if (data === 2) {
        //       return h('span', {}, '')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          render: (h, params) => {
            let row = params.row
            let optionArr = []
            if (row.rlhdGroupList.length > 0) {
              row.rlhdGroupList.forEach(item => {
                if (item.name) {
                  optionArr.push(item.name)
                }
              })
            }
            return h('div', {}, optionArr.join(''))
          }
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        },
        {
          title: '',
          key: '',
          width: 260,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.spotsSeeId = params.row.cl001
                    this.addActive = true
                    this.$refs.spotsTable.getTableList(this.spotsSeeId)
                  }
                }
              }, ''),
              // 
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.taskEdit.taskEdit = true
                  }
                }
              }, ''),
              // 
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '',
                      content: '',
                      onOk: () => {
                        this.infoDel(params.row.cl001)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '')
            ])
          }
        }
      ],
      data1: [],
      /*  */
      total: 0,
      /*  */
      pageNum: 1,
      /*  */
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /*  */
      editDefaultData: {},
      /*  */
      addActive: false,
      /* id */
      spotsSeeId: 0,
      // 
      selectRow: [],
      multipleSelection: [],
      /*  */
      obj: '',
      cl002: '', // 
      cl010: '', // 
      cl016: '', // 
      /*  */
      codeData: []
    }
  },
  components: {
    QueryBox,
    // 
    taskAdd,
    // 
    taskEdit,
    // 
    IssueList,
    // 
    spotsTable
  },
  watch: {
    multipleSelection (data) { // row
      this.selectRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectRow.push(item.cl001)
        })
      }
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // table
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
    this.getCodeMenu()
  },
  methods: {
    /*  */
    searchSrldId (obj) {
      this.cl002 = obj.cl002
      this.cl010 = obj.cl010
      this.cl016 = obj.code
      this.pageNum = 1
      this.getTableList()
    },
    /*  */
    getTableList () {
      this.loading = true
      getTaskList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          cl002: this.cl002,
          cl010: this.cl010,
          cl016: this.cl016
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios:  */
    infoDel (e) {
      deleteTask({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '' })
          // --
          if (this.data1.length === 1 && this.pageNum > 1) {
            this.pageNum = this.pageNum - 1
          }
          this.getTableList()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    // checkbox
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    // 
    handleIssue () {
      this.$refs.issueList.issue = false
      if (this.selectRow.length < 1) {
        this.$Notice.error({ title: '' })
      } else if (this.selectRow.length > 1) {
        this.$Notice.error({ title: '' })
      } else {
        this.$refs.issueList.issue = true
      }
    },
    /*  */
    signOut (addActive) {
      this.addActive = addActive
      if (addActive === false) {
        this.spotsSeeId = ''
      }
    },
    /*  */
    getClearQueryData () {
      this.pageNum = 1
      this.getTableList()
    },
    // 
    uploadSuccess (res, file) {
      if (res.code === '0000') {
        this.$Notice.success({ title: '' })
        this.getTableList()
      } else {
        this.$Notice.error({ title: res.msg })
      }
    },
    /*  */
    handleExport () {
      exportFile({
        data: {
          cl002: this.cl002,
          cl010: this.cl010,
          cl016: this.cl016
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.excelPath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /* axios:  */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.codeData = res.data.data
          this.codeData = this.valComCode(this.codeData)
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /* value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px 0;
        text-align: right
      }
    }
    .spotsTable {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin: 0 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
  }
</style>
