<template>
  <div id="issueList">
    <Modal
      id="taskIssueModal"
      v-model="issue"
      title=""
      :loading="modalIssueLoading"
      @on-cancel="handleCancel">
      <Form ref="formInline" inline :label-width="75" :model="formInline" class="formInline">
        <FormItem label="">
          <Input type="text" v-model="formInline.sa019" style="width: 150px;" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formInline.sa012" style="width: 150px;" placeholder=""></Input>
        </FormItem>
        <FormItem :label-width="0" class="ml20">
          <Button type="primary" size="small" icon="ios-search" @click.stop="getSearchIssueList" class="mr10"></Button>
          <Button type="primary" size="small" icon="md-refresh" @click.stop="cleardata"></Button>
        </FormItem>
      </Form>
      <div>
        <Table border stripe :loading="IssueLoading" :columns="IssueColumns1" :data="IssueData" highlight-row :max-height="IssueTableHeight"
               ref="IssueMultipleTable"
               @on-selection-change="handleIssueSelectionChange">
        </Table>
        <div class="page_box">
          <Page :total="IssueTotal" ref="IssuePages" :page-size="IssuePageSize" :current.sync="IssuePageNum" @on-change="IssuePageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10"></i-button>
        <Button type="primary" @click="handleIssueTask"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 
  userList,
  // 
  sharePoint
} from '@/api/taskManager'
export default {
  name: 'IssueList',
  data () {
    return {
      /*  */
      issue: false,
      /*  loading  */
      modalIssueLoading: false,
      /*  */
      formInline: {
        sa019: '',
        sa012: ''
      },
      /*  */
      IssueTableHeight: Math.floor((window.innerHeight - 350 - 40) / 48) * 48 + 40,
      /*  */
      IssueColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.IssuePageNum - 1) * this.IssuePageSize + 1)
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 200,
          key: 'sa008'
        },
        {
          title: '',
          align: 'center',
          minWidth: 200,
          key: 'sa012'
        },
        {
          title: '',
          align: 'center',
          minWidth: 200,
          key: 'sa019'
        }
      ],
      IssueData: [],
      /*  */
      IssueTotal: 0,
      IssuePageNum: 1,
      IssuePageSize: 10,
      /*  */
      IssueLoading: false,
      // 
      selectIssueRow: [],
      multipleIssueSelection: [],
      taskParamsArr: []
    }
  },
  props: {
    /* id */
    taskIdArr: {}
  },
  created () {
    this.getIssueList()
  },
  watch: {
    multipleIssueSelection (data) { // row
      this.selectIssueRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectIssueRow.push(item.sa001)
        })
      }
    },
    taskIdArr (val) {
      this.taskParamsArr = val
    }
  },
  mounted () {
    // table
    window.addEventListener('resize', () => {
      this.IssueTableHeight = Math.floor((window.innerHeight - 350 - 40) / 48) * 48 + 40
      this.getIssueList()
    }, false)
  },
  methods: {
    /*  */
    getSearchIssueList () {
      this.IssuePageNum = 1
      this.getIssueList()
    },
    /*  */
    getIssueList () {
      this.IssueLoading = true
      userList({
        data: {
          pageSize: this.IssuePageSize,
          pageNumber: this.IssuePageNum,
          sa019: this.formInline.sa019,
          sa012: this.formInline.sa012
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.IssueData = res.data.data.rows
          this.IssueTotal = res.data.data.total
          this.IssueLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    IssuePageChange (pageNum) {
      this.IssuePageNum = pageNum
      this.getIssueList()
    },
    // checkbox
    handleIssueSelectionChange (val) {
      this.multipleIssueSelection = val
    },
    // 
    handleIssueTask () {
      if (this.selectIssueRow.length < 1) {
        this.$Notice.error({ title: '' })
      } else {
        this.handleIssue(this.selectIssueRow)
      }
    },
    // 
    handleIssue (e) {
      sharePoint({
        data: {
          taskId: this.taskParamsArr.join(','),
          uidList: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '' })
          this.handleCancel()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    cleardata () {
      this.formInline = {}
      this.getSearchIssueList()
    },
    handleCancel () {
      this.issue = false
      this.selectIssueRow = []
      this.multipleIssueSelection = []
      this.cleardata()
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  .formInline {
    overflow: hidden;
    .ivu-form-item {
      margin-bottom: 16px;
    }
  }
  .mr10 {
    margin-right: 10px;
  }
  .ml20 {
    margin-left: 20px;
  }
  .page_box {
    margin-top: 15px;
    text-align: right;
  }
</style>
